import * as THREE from 'three';
import * as $ from 'jquery';
import { DsShaderMaterial } from './ds-shader-material.js';
import { DsShaderScene } from './ds-shader-scene.js';

class SDFRenderer {

	constructor(canvasRef) {
		console.log("created");

		const uniforms = {
			time: { value: performance.now() },
			radius: { value: 0.5 },
			offset: { value: 0.5 },
			smoothness: { value: 1 },
			box_size: { value: 0.25 },
			box_radius: { value: 0.1 },
			bool_offset: { value: 0.125 },
			bool_smink: { value: 0.1 },
			annular_thickness: { value: 0.1 },
			shape_size: { value: 1 },
			secondary_size: { value: 1 }
		};

		this.canvas =canvasRef;
		this.scenes = [];

		// Create the scene in each shader-container.
		$(".shader-container").each((index, element) => {
			var shader = $(element).data("shader");			
			this.scenes[index] = new DsShaderScene(element, shader, uniforms);
		});

		// Configure each shader-slider.     
		$('.shader-slider').each(function (index, element) {

			var input = $(element).find('.range-slider__input');
			var value_text = $(element).find('.range-slider__value-text');

			// Set all the input slider values to their corresponding uniform value.
			var uni = $(element).data("uniform");

			if (uni) {

				$(input).val(uniforms[uni].value);

				// Set all the value text to the slider value.
				value_text.each(function () {
					var value = $(this).prev().attr('value');
					$(this).html(parseFloat(value).toFixed(2));
				});

				// For each input slider, on change.
				input.on('input', function () {

					// Update the value text.
					$(this).next(value_text).html(parseFloat(this.value).toFixed(2));

					// Update the uniform to this new value.
					var uni = $(element).data("uniform");
					if (uni != null) {
						uniforms[uni].value = $(this).val();
					}

					// Render
					this.render();
				});
			}
		});

		this.renderer = new THREE.WebGLRenderer({ canvas: this.canvas, antialias: true, alpha: true });
		this.renderer.setClearColor(0xffffff, 0);
		this.renderer.clear();


		//const gl = this.renderer.domElement.getContext('webgl') || this.renderer.domElement.getContext('experimental-webgl');
		//gl.getExtension('OES_standard_derivatives');

		this.animate();
	}

	updateSize = () => {
		const width = this.canvas.clientWidth;
		const height = this.canvas.clientHeight;

		if (this.canvas.width !== width || this.canvas.height !== height) {
			this.renderer.setSize(width, height, false);
		}
	}

	animate = () => {

		this.render();
		requestAnimationFrame(this.animate);

	}


	render = () => {
		
		this.updateSize();

		this.canvas.style.transform = `translateY(${window.scrollY}px)`;

		this.renderer.setClearColor(0xffffff, 0);
		this.renderer.setScissorTest(false);
		this.renderer.clear();

		this.renderer.setClearColor(0xffffff, 255);
		this.renderer.setScissorTest(true);

		const renderer = this.renderer;

		this.scenes.forEach(function (scene) {
			// get the element that is a place holder for where we want to
			// draw the scene
			const element = scene.element;

			// get its position relative to the page's viewport
			const rect = $(element)[0].getBoundingClientRect();

			// check if it's offscreen. If so skip it
			if (rect.bottom < 0 || rect.top > renderer.domElement.clientHeight ||
				rect.right < 0 || rect.left > renderer.domElement.clientWidth) {

				return; // it's off screen
			}

			// set the viewport
			const width = rect.right - rect.left;
			const height = rect.bottom - rect.top;
			const left = rect.left + window.scrollX;
			const bottom =  renderer.domElement.clientHeight - rect.bottom;

			renderer.setViewport(left, bottom, width, height);
			renderer.setScissor(left, bottom, width, height);

			const camera = scene.camera;

			renderer.render(scene.scene, camera);

		});

	}

}

export default SDFRenderer;