import { useNavigate } from "react-router-dom";

function PostPreview(props) {
	let navigate = useNavigate();
	const onClick = () => {
		navigate(`./${props.post.id}`);
	}


	return (


		<section>
			<div className="container flex blog-entry" onClick={onClick}>

				<span className="col blog-title">
					<h1>{props.post.title}</h1>
					<h2>{props.post.subtitle}</h2>
					{props.post.snippet.map(l => {return (
						<p>{l}</p>
					);})}
				</span>

				<div className="col blog-featured">
					
						<img src={props.post.featuredImg} />
					



				</div>
			</div>
		</section>


	);

}

export default PostPreview;