import { useId } from "react";
import { useParams } from "react-router-dom";
import ShaderSlider from "./shader-helpers/ShaderSlider";
import SignedDistanceFunctions from "./posts/SignedDistanceFunctions";

function Post(props) {

	let { id } = useParams();

	function renderInternal() {
		switch(id){
			case "signed-distance-functions":
				return <SignedDistanceFunctions />;
		}
	}

	let blog = require("./../../config/blog.json");
	var post = blog.posts.find(p => p.id==id);

	return (
		<div>
			<div className="container">
				<a href="./blog">
					<div className="header--breadcrumb">

						<svg xmlns="http://www.w3.org/2000/svg" height="48" width="48">
							<path d="M20 44 0 24 20 4 22.8 6.85 5.65 24 22.8 41.15Z" />
						</svg>

						{post ? post.title : "Not Found"}
					</div>
				</a>
			</div>
			{
				renderInternal()
			}
		</div>

	);



}

export default Post;