
import * as THREE from 'three';
import * as $ from 'jquery';
import { DsShaderMaterial } from './ds-shader-material.js';

export class DsShaderScene {

	constructor(container, fragmentShaderPath, uniforms) {
		this.container = container;
		this.uniforms = uniforms;

		this.camera = new THREE.OrthographicCamera(-1, 1, 1, -1, 0.01, 10);
		this.camera.position.z = 1;

		this.scene = new THREE.Scene();

		var size = Math.min($(this.container).width(), $(this.container).width())
		this.element = $('<div />', {
			class: "shader-area"
		});
		$(container).prepend(this.element);



		this.loadScene(fragmentShaderPath);
	}

	readFileAsync = (file) => {
		return new Promise((resolve, reject) => {
			let reader = new FileReader();

			reader.onload = () => {
				resolve(reader.result);
			};

			reader.onerror = reject;

			reader.readAsText(file);
		})
	}

	async loadScene(fragmentShaderPath) {
	
		let frag = await fetch("../shaders/" + fragmentShaderPath +".hlsl").then(r => r.text());
		let vert = await fetch("../shaders/quad.hlsl").then(r => r.text());
		
		const material = new DsShaderMaterial(vert, frag, this.uniforms);

		this.mesh = new THREE.Mesh(new THREE.PlaneGeometry(2, 2), material);
		this.scene.add(this.mesh);
	}
}
