import * as THREE from 'three';

export class DsShaderMaterial extends THREE.RawShaderMaterial {

	constructor(vert, frag, uni) {
		super({ vertexShader: vert, fragmentShader: frag, uniforms: uni });

		window.requestAnimationFrame(this.loop);
	}

	loop = (timestamp) => {
		window.requestAnimationFrame(this.loop);
		this.uniforms.time.value = timestamp;
	};
}