import { useState } from "react";

function ShaderSlider(props) {

	const [v, setV] = useState(0);

	var onValueChanged = (val) => {
		setV(val);
	}

	return (
		<div className="range-slider shader-slider" data-uniform={props.uniform}>
			<span className="range-slider__label">
				<h2>{props.label}</h2>
			</span>
			<input className="range-slider__input" type="range" defaultValue={props.defaultValue} min={props.minValue} max={props.maxValue} step={props.step} onChange={onValueChanged} />
			<span className="range-slider__value-text">{props.defaultValue}</span>
		</div>
	);

}

export default ShaderSlider;